import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { FingerprintComponent } from '../fingerprint/fingerprint.component';
import Swal from 'sweetalert2';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { ThemeService } from '../../../shared/services/theme.service';
import { LayoutService } from '../../../shared//services/layout.service';
import { v4 as uuid } from 'uuid';
import { ApiService } from 'app/shared/services/api.service';
import { SecurityDto } from 'app/views/maintenance/user-maintenance/add-user-maintenance/add-user-maintenance.component';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {

  signinForm: FormGroup;
  security : SecurityDto;
  hide=true;
  themeValue: any;
  browserKey: any;
  notifyPswdExpry: number;
  pswdExpiry: number;
  currentDate: any;
  changePwdDate: any;
  differnceInTime: any;
  differnceInDays: number;
  userResp:any;
  user : User;
  finalDiff: number;
  constructor(private fb: FormBuilder, 
    private layout: LayoutService,
    private router: Router,
    private jwtService: JwtAuthService,
    public themeService: ThemeService, 
    public dialog: MatDialog,
    private toastr: ToastrService,
    private apiService : ApiService,
    private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.browserKey = uuid();
    this.signinForm = this.fb.group({
        username: ['', Validators.required],
        password: ['', Validators.required],
        systemCode: [ this.browserKey ]
    });
    this.apiService.getSecurityPolicyDetails().subscribe((policy) => {
       this.security = policy[0];
    });
  }

  onSubmit() {
    if (!this.signinForm.invalid) {
      this.spinner.show();
      this.jwtService.loginIcut(this.signinForm.value).subscribe((res: any) => {
        console.log("ress", res);
        this.userResp = res
        this.spinner.hide();
        if(res?.data == null && res.message == "Account blocked, Max failed attempts reached"){
          this.toastr.error(`${res.message}`,`Error!`,{
            progressAnimation:'decreasing',
            progressBar:true,
            positionClass:'toast-top-right',
            timeOut: 3000,
          });
        }
        else if (res) {
          this.notifyPswdExpry = this.security.notifyPasswordExpiryInDays;
          this.pswdExpiry = this.security.pswdExpiry;
          if (this.pswdExpiry === 0 && this.notifyPswdExpry === 0){
          }
          else{
            this.currentDate = new Date();
            this.changePwdDate = new Date(this.userResp.pwdChangeDate);
            console.log(this.changePwdDate);
            this.differnceInTime = this.currentDate - this.changePwdDate;
            console.log(this.differnceInTime)
            this.differnceInDays = Math.floor(
              this.differnceInTime / (1000 * 3600 * 24)
            );
            this.finalDiff = this.pswdExpiry - this.differnceInDays;
            console.log(this.finalDiff, this.pswdExpiry, this.differnceInDays);
            if (this.finalDiff === 0 || this.finalDiff <= 0) {
              this.toastr.error(`Error!`,`Your Password is expired. Please reset your password.`,{
                progressAnimation:'decreasing',
                progressBar:true,
                positionClass:'toast-top-right',
                timeOut: 3000,
              });
             
            }
            else{
                console.log("if res", res);
                this.jwtService.setUserAndToken(res, true);
                localStorage.setItem('branchName', res.branchName);
                localStorage.setItem('userName', res.userName);
                localStorage.setItem("browserId", this.browserKey);
                console.log(this.finalDiff);
                if (this.finalDiff > 0 && this.finalDiff <= this.notifyPswdExpry) {
                  this.toastr.error(
                    "Your password will expire in " +
                      `${this.finalDiff}` +
                      " day(s)",
                    ""
                  );
                
                }
                this.router.navigateByUrl('/others/dashboard');
                this.showToast(res);
            }
          }
        }
      }, (err: any) => {
        console.log("err", err);
        this.spinner.hide();
        if (err.error) {
          this.toastr.error(`${err.error.message}`,`Error!`,{
            progressAnimation:'decreasing',
            progressBar:true,
            positionClass:'toast-top-right',
            timeOut: 3000,
          });
        }
      });
    }
    else if (
      this.signinForm.value.password === "" ||
      this.signinForm.value.username === ""
    ) {
      this.toastr.error(`Error`,`Please enter login credentials.`,{
        progressAnimation:'decreasing',
        progressBar:true,
        positionClass:'toast-top-right',
        timeOut: 3000,
      });
    }
  }
  onChange(e): void {
    console.log("e", e);
    //  if (e.checked) {
    //   this.themeService.changeTheme('egret-navy','egret-dark-purple');
    // } 
    // else {
    //     this.themeService.changeTheme('egret-dark-purple','egret-navy');
    //   }
    // }
  /*  if (this.themeValue.value == 'light') {
      console.log("inside if");
      this.themeService.changeTheme1('egret-navy');
    } */
  }
  changeTheme1(theme) {
    //this.layout.publishLayoutChange({ matTheme: theme.name }) 
      this.layout.applyMatTheme(theme);
  }

  openDialog() {
    const dialogRef = this.dialog.open(FingerprintComponent);

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

 showToast(user:any){
  this.toastr.success(`${user.email}`,`Welcome, ${user.userName}!`,{
    progressAnimation:'decreasing',
    progressBar:true,
    positionClass:'toast-top-right',
    timeOut: 3000,
  });
 }

}

export class User {
 
  userId: string;
  userName: string;
  tellerName:string; 
  pwdChangeDate:Date;
  branchName:string;
  currency:string;
  email:string;
}